import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)
const login = resolve => require(['../view/login.vue'], resolve)
const register = resolve => require(['../view/register.vue'], resolve)
const list = resolve => require(['../view/list.vue'], resolve)
export default new Router({
  routes: [
    {
      path: '/',
      name: 'login',
      component: login
    },
    {
      path: '/view',
      name: 'register',
      component: register
    },
    {
      path: '/list',
      name: 'list',
      component: list
    }
  ]
})



