<template>
<div>
<el-button type="text" @click="dialogFormVisible = true">添加图书</el-button>
<el-dialog title="添加图书" :visible.sync="dialogFormVisible">
  <el-form :model="bookform" ref="bookform">
    <el-form-item label="图书名称" :label-width="formLabelWidth">
      <el-input v-model="bookform.name" auto-complete="off"></el-input>
    </el-form-item>
    <el-form-item label="作者" :label-width="formLabelWidth">
      <el-input v-model="bookform.author" auto-complete="off"></el-input>
    </el-form-item>
  </el-form>
  <div slot="footer" class="dialog-footer">
    <el-button @click="dialogFormVisible = false">取 消</el-button>
    <el-button type="primary" @click="submit('bookform')">确 定</el-button>
  </div>
</el-dialog>
</div>
</template>
<script>
import { Message } from "element-ui";
export default {
  data() {
    return {
      dialogFormVisible: false,
      bookform: {
        name: "",
        region: "",
        date1: "",
        date2: "",
        delivery: false,
        type: [],
        resource: "",
        desc: ""
      },
      formLabelWidth: "120px"
    };
  },
  methods: {
    submit(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$http({
            url: "http://120.24.5.178:8100/book/add",
            method: "POST",
            body: JSON.stringify({
              title: this.bookform.name,
              author: this.bookform.author
            }),
            headers: {
              "Content-Type": "application/json"
            }
          }).then(function(response) {
            console.log(response.data.code);
            if (response.data.code === 0) {
              Message.success("添加成功");
            } else {
              Message.warning(response.data.msg);
            }
          });
        }
      });
    }
  }
};
</script>



